.footer {
    position: fixed;
    bottom: 0vh;
    background-color: black;
    width: 100%;
    height: 10vh;
}

.player__container {
    background-color: black;
    margin: 0 auto;
    width: 95%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.player__songname {
    width: 15%;
    color: white;
}

.player__songtitle {
    font-weight: 700;
}

.player__controls {
    display: flex;
    flex-direction: column;
    gap: .5rem;
    width: 50%;
    align-items: center;
}

.buttons__container {
    width: 30%;
}

.timer__container {
    width: 100%;
    color: white;
}

.timer__input {
    width: 90%;
}

.buttons__container,
.timer__container,
.volume__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.player__volume {
    display: flex;
    align-items: center;
    gap: .5rem;
}

.player__btn {
    color: white;
}