.playlists__container {
    position: fixed;
    top: 6vh;
    height: 94vh;
    width: 10vw;
    background-color: rgb(53, 53, 53);
}

.playlists {
    padding-top: 1rem;
    padding-left: .5rem;
}

.playlists__title {
    color: white;
    font-size: 2rem;
    font-weight: 700;
    padding-bottom: 1rem;
}

.playlists__list {
    display: flex;
    flex-direction: column;
    gap: .5rem;
    padding-inline: .5rem;
}

.playlists__btn-add {
    padding: .5rem;
    background-color: white;
    border-radius: 1rem;
    border-style: solid;
    border-color: white;
    text-transform: uppercase;
    font-weight: 700;
    transition: all .3s;
}

.playlists__btn-add:hover {
    background-color: black;
    border-color: black;
    color: white;
    cursor: pointer;
}

.playlists__item {
    color: white;
    background-color: rgb(26, 26, 26);
    padding: 1rem;
    text-decoration: none;
    font-weight: 700;
    transition: all .3s;
}

.playlists__item:hover {
    cursor: pointer;
    background-color: darkgray;
    color: black;
}