.header {
    height: 6vh;
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    background-color: black;
}

.header__nav {
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding-inline: 3rem;
}

.header__nav-buttons,
.header__nav-search {
    display: flex;
    gap: 2rem;
}

.btn__borderless {
    background-color: none;
}

.btn__icon {
    background-color: none;
    color: grey;
}

.btn__icon:hover {
    cursor: pointer;
    color: white;
}

.btn__home {
    background-color: black;
    border-radius: 50%;
    padding: .5rem;
    transition: all .3s;
}

.home__icon {
    color: white;
}

.btn__home:hover {
    background-color: white;
}

.btn__home:hover .home__icon {
    color: black;
}