.artistcard__container {
    transition: all .3s;
    border-radius: 1.5rem;
}

.artistcard__container:hover {
    background-color: gray;
}

.artistcard {
    padding: .5rem;
    display: flex;
    flex-direction: column;
    text-decoration: none;
}


.artistcard__img {
    border-radius: 1rem;
}

.artistcard__title {
    color: gray;
    text-decoration: none;
    padding-top: 1rem;
    font-weight: 700;
    transition: all .3s;
}

.artistcard__container:hover .artistcard__title {
    color: black;
}